<template>
    <div class="card-info-body">
        <dth-header></dth-header>
        <div class="doInterval-action" v-show="isDoInterval">
            <span class="num">{{ backNum }}</span>秒后自动跳转到首页...
        </div>
        <dth-back-top></dth-back-top>
        <dth-back-home></dth-back-home>
        <dth-flow-node>
            <li>
                <div class="flow-node0 idCard item"></div>
                <span class="flow-text">身份证识别</span>
            </li>
            <li class="diliver current"></li>
            <li>
                <div  class="flow-node1 unCompareAuth item"></div>
                <span class="flow-text">人证对比</span>
            </li>
            <li class="diliver next"></li>
            <li>
                <div class="flow-node2 unInvoice item"></div>
                <span class="flow-text">开票二维码识别</span>
            </li>
            <li class="diliver next"></li>
            <li>
                <div class="flow-node3 unPrint item"></div>
                <span class="flow-text">发票打印</span>
            </li>
        </dth-flow-node>
        <div class="card-status">
            <h5>请将身份证放置在扫描区（目前只支持二代身份证）</h5>
            <div class="card-body">
                <img class="statusImg" :src="statusImg" />
                <div class="card-status-content" v-show='isReadStatus'>{{ cardReadStatus }}</div>
            </div>
            <div class="repeatReadCard" @click="reloadCurrentPage" v-show="repeatReadCard">
                重新开始身份证识别
            </div>
        </div>
        <div class="cardInfo" v-show="iscardInfo">
            <!--<div class="cardInfo-pic">
                <img width="100" :src="cardImg" />
            </div>-->
            <el-form class="cardForm" label-width="102px">
                <el-form-item class="cardInput" label="姓名 :">
                    <el-input
                        v-model="cardname"
                        ref="cardname"
                        placeholder="姓名"
                        name="cardname"
                        tabindex="1"
                        autocomplete="on"
                    ></el-input>
                </el-form-item>
                <el-form-item class="cardInput" label="身份证号 :">
                    <el-input
                        v-model="number"
                        ref="number"
                        placeholder="身份证号"
                        name="number"
                        tabindex="2"
                        autocomplete="on"
                    ></el-input>
                </el-form-item>
            </el-form>

            
            
        </div>
        <el-row class="next-step" style="display: none">
                <!--<span style="color: #ff0000">扫描完成，温馨提示，请收好您的证件！</span>-->
                <el-button type="primary" @click="gocardInfo">下一步</el-button>
            </el-row>
        <audio v-show="visible" controls="controls" autoplay>
            <source src="../assets/readCardId.mp3" type="audio/mpeg">
        </audio>
        <audio v-show="visible" controls="controls" id="leaveCard">
            <source src="../assets/leaveCardId.mp3" type="audio/mpeg">
        </audio>
        <dth-footer></dth-footer>
    </div>
</template>
<script>
import $ from 'jquery'
// import { Message } from 'element-ui'
import { setStorage, doInterval } from '@/utils/auth'
import DthHeader from '@/components/DthHeader/DthHeader.vue'
import DthFooter from '@/components/DthFooter/DthFooter.vue'
import DthBackHome from '@/components/DthBackHome/DthBackHome.vue'
import DthBackTop from '@/components/DthBackTop/DthBackTop.vue'
import DthFlowNode from '@/components/DthFlowNode/DthFlowNode.vue'

export default {
    name: "CardInfo",
    components: { DthHeader, DthFooter, DthBackHome, DthBackTop, DthFlowNode},
    data() {
        return {
            visible: false,
            title: 'CardInfo',
            isNextShow: false,
            cardStatus: '进行中...',
            cardname: '',
            number: '',
            cardReadStatus: '身份证信息读取中，请勿移开身份证！',
            statusImg: require('../assets/idCardFlow0.png'),
            cardImg: require('../assets/logo.png'),
            timer: null,
            loopTimer: null,
            iscardInfo: false,
            repeatReadCard: false,
            isReadStatus: true,
            backNum: 180,
            isDoInterval: false,
            doIntervalTimer: null,
        }
    },
    mounted () {
        this.$nextTick(() => {
            const element = document.documentElement;
            if(element.requestFullscreen) {
                
            element.requestFullscreen();
            } else if (element.mozRequestFullScreen){
            element.mozRequestFullScreen();
            } else if(element.webkitRequestFullscreen) {
            element.webkitRequestFullscreen();
            } else if (element.msRequestFullscreen) {
            element.msRequestFullscreen();
            }
        })
        document.addEventListener('keypress', this.resetTimer, false);
        document.addEventListener('keyup', this.resetTimer, false);
        document.addEventListener('keydown', this.resetTimer, false);
        document.addEventListener('click', this.resetTimer, false);
        clearInterval(this.loopTimer)
        this.loopTimer = setInterval(() => {
            this.doInterval('doInvoice')
        }, 1000)
        this.OpenDevice()
        this.GetCardinfoInit()
    },
    destroyed() {
      clearInterval(this.timer)
      clearInterval(this.loopTimer)
    },
    methods: {
        resetTimer() {
            this.backNum = 180;
            clearInterval(this.loopTimer)
            this.loopTimer = setInterval(() => {
                this.doInterval('doInvoice')
            }, 1000)
        },
        doInterval: doInterval,
        reloadCurrentPage() {
            location.reload();
        },
        GetCardinfoInit() {
            this.timer = setInterval(() => {
                this.GetCardinfo()
            }, 500)
        },
        GetCardinfo() {
            $.ajax({
                    dataType: "JSONP",                    
                    type: "get",
                    url: "http://localhost:8080/api/ReadMsg",
                    success: (data) => {
                        if(data.retcode == 0){
                            clearInterval(this.timer);
                            this.isReadStatus = false;
                            this.isReadStatus = true;
                            this.statusImg = require('../assets/idCardFlow1.png');
							this.cardname = data.name;
							this.number = data.cardno;	
                            this.cardImg = `data:image/jpg;base64, ${data.photobase64}`;
                            setStorage('cardInfo', {userName: data.name, idNumb: data.cardno})
                            // this.isNextShow = true;
                            document.getElementById('leaveCard').play();
                            setTimeout(() => {
                                this.$router.push({path: '/faceInfo'})
                            }, 3000)
						}else{
							
                            console.info(`读二代证失败，错误码为：${data.retcode}, ${data.errmsg}`)
						}
                    },
                    error: (e) => {
                        setTimeout(() => {
                            this.cardname = "";
							this.number = "";
                            this.cardImg = "";
                            this.cardReadStatus = '身份证识别失败，请重新识别！';
                            this.repeatReadCard = true;
                            this.isReadStatus = false;
                            this.iscardInfo = false;
                            this.statusImg = require('../assets/idCardFailed0.png');
                            clearInterval(this.timer)
                        }, 2000)
                        console.info(`执行失败：${e.status}, ${e.statusText}`)
                    }
                });
        },
        OpenDevice() {
            
                    // let cpucardno = document.getElementById('cardno').value;
                    $.ajax({
                        dataType: "JSONP",                    
                        type: "get",
                        url: "http://localhost:8080/api/OpenDevice",
                        data : { Port:"100",Baud:"0" },
                        success: (data) => {
                            if(data.retcode == 0)
                            {
                                console.log(data)
                            }else
                            {
                                console.info(`打开设备失败，错误码为：${data.retcode}, ${data.errmsg}`)
                            }
                        },
                        error: (e) => {
                            console.info(`执行失败：${e.status}, ${e.statusText}`)
                        }
                    });
        },
        gocardInfo() {
            sessionStorage.setItem('cardInfo', JSON.stringify({userName: "朱小龙", idNumb: "362426198807233514"}))
            this.$router.push({name: "faceInfo"})
        }
    },
}
</script>
<style lang="scss">
.cardInfo-pic {
    margin-bottom: 20px;
}
.doInterval-action {
      position: absolute;
    width: 58%;
    text-align: center;
    padding: 15px 0;
    top: 0;
    background-color: #e4cc1f;
    color: #FFF;
    font-size: 20px;
    margin: 0 auto;
    left: 50%;
    margin-left: -29%;
    top: 110px;
    border-radius: 0px;
  .num {
    font-size: 32px;
    vertical-align: middle;
    display: inline-block;
    margin-right: 5px;
  }
}


.repeatReadCard {
    cursor: pointer;
    width: 502px;
    margin: 10px auto 20px auto;
    text-align: center;
    padding: 15px 0;
    background-color: #fdd41b;
    border-radius: 6px;
    font-weight: bold;
}
.card-status {
    width: 100%;
    h5 {
        margin-bottom: 28px;
        color: #224c88;
        font-size: 1.4em;
        margin-top: 38px;
    }
}
.card-body {
    width: 502px;
    height: 284px;
    margin: 0 auto 5px auto;
    border: 1px solid #ccc;
    position: relative;
    border-radius: 20px;
    box-shadow: 2px 2px 5px #dedede;
    .card-status-content, .statusImg {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
    .card-status-content {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(0, 0, 0,.7);
        color: #FFD200;
        border-radius: 20px;
        font-weight: bold;
        font-size: 1.4em;
    }
}

.cardForm {
    display: flex;
    .el-input--medium .el-input__inner {
        border: 0;
        font-weight: bold;
        font-size: 18px;
        padding-left: 0;
    }
    .el-form-item--medium .el-form-item__label {
        font-size: 18px;
    }
}
.cardInput {
    flex: 1;
}

.readCardId {
    .el-form-item__content{
        text-align: left;
    }
 }
 .next-step {
    width: 502px;
    display: flex;
    margin: 0 auto;
    justify-content: flex-end;
 }
.card-info-body {
    height: 100%;
    height: 100%;
    overflow: hidden;
    
}
.video{
    width: 100%;
}
.canvas {
    width: 100%;
    border: 0;
}
.cardInfo{
    width: 742px;
    margin: 36px auto 0 auto;
    flex: 1;
}

.capture{
    padding: 8px 28px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: 0 1px 4px #ccc;
    margin: 20px 0;
    background-color: #146aaf;
    color: #FFF;
    cursor: pointer;
    outline: none;
}
</style>